<template>
  <div class="sider-bar">
    <component :is="currentSiderBar"></component>
  </div>
</template>

<script>
import GenshinImpactSideBar from '@/components/GenshinImpactSiderBar.vue';
import StarRailSideBar from '@/components/StarRailSiderBar.vue';
import ZenlessZoneZeroSideBar from '@/components/ZenlessZoneZeroSiderBar.vue';

export default {
  name: 'SiderBar',
  components: {
    GenshinImpactSideBar,
    StarRailSideBar,
    ZenlessZoneZeroSideBar,
  },
  computed: {
    currentSiderBar() {
      const componentNameMap = {
        '/Genshin-Impact': 'GenshinImpactSideBar',
        '/Star-Rail': 'StarRailSideBar',
        '/Zenless-Zone-Zero': 'ZenlessZoneZeroSideBar',
      };

      return componentNameMap[this.$route.path] || 'GenshinImpactSideBar';
    },
  },
};
</script>

<style scoped>
  .sider-bar {
    width: 250px;
    background-color: #f5f5f5;
    box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1);
    overflow-y: atuo;
    padding: 1rem;
  }
</style>