<template>
  <div class="main-layout">
    <component :is="currentContentComponent"></component>
  </div>
</template>

<script>
import GenshinImpact from '@/components/GenshinImpact.vue';
import StarRail from '@/components/StarRail.vue';
import ZenlessZoneZero from '@/components/ZenlessZoneZero.vue';

export default {
  name: 'MainLayout',
  components: {
    GenshinImpact,
    StarRail,
    ZenlessZoneZero,
  },
  computed: {
    currentContentComponent() {
      const componentNameMap = {
        '/Genshin-Impact': 'GenshinImpact',
        '/Star-Rail': 'StarRail',
        '/Zenless-Zone-Zero': 'ZenlessZoneZero',
      };

      return componentNameMap[this.$route.path] || 'GenshinImpact';
    },
  },
};
</script>