import { createRouter, createWebHashHistory} from 'vue-router'
import MainLayout from '@/components/MainLayout.vue'
import GenshinImpact from '@/components/GenshinImpact.vue'
import StarRail from '@/components/StarRail.vue'
import ZenlessZoneZero from '@/components/ZenlessZoneZero.vue'

const routes = [
    {
        path: '/',
        component: MainLayout,
            children: [
            {
                path: '',
                redirect: '/Genshin-Impact',
            },
            {
                path: '/Genshin-Impact',
                component: GenshinImpact,
                meta: {title: "GenshinImpact"},
            },
            {
                path: '/Star-Rail',
                component: StarRail,
                meta: {title: "StarRail"},
            },
            {
                path: '/Zenless-Zone-Zero',
                component: ZenlessZoneZero,
                meta: {title: "ZenlessZoneZero"},
            },
        ],
    },
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
});

export default router;
