<template>
  <div class="header">
    <img src="../assets/logo.png">
    <div class="logo">
      <div class="nav-item">Web For Star</div>
    </div>
    <div class="nav">
      <router-link :to="'/Genshin-Impact'" :class="{ 'nav-item': true, 'active': $route.path === '/Genshin-Impact' }">原神</router-link>
      <router-link :to="'/Star-Rail'" :class="{ 'nav-item': true, 'active': $route.path === '/Star-Rail' }">崩坏星穹铁道</router-link>
      <router-link :to="'/Zenless-Zone-Zero'" :class="{ 'nav-item': true, 'active': $route.path === '/Zenless-Zone-Zero' }">绝区零</router-link>
    </div>
    <div class="action" style="display: flex; margin-left: auto;">
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css" crossorigin="anonymous" referrerpolicy="no-referrer" />
      <button class="nav-item register-btn" @click="openRegisterModal">
        <span class="icon"></span>
        注册
      </button>
       <button class="nav-item login-btn">
        <span class="icon"></span>
        登录
        </button>
    </div>

    <div class="modal" v-if="isRegisterModalOpen" @click.self="closeRegisterModal">
      <div class="modal-content" @click.stop>
        <h2>注册</h2>
        <form @submit.prevent="submitRegistration">
          <input type="text" placeholder="用户名或邮箱" v-model="username" required>
          <input type="password" placeholder="密码" v-model="password" required>
          <input type="password" placeholder="确认密码" v-model="confirmPassword" required>
          <button type="submit">注册</button>
        </form>
      </div>
  </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'PageHeader',
  data() {
    return {
      isRegisterModalOpen: false,
      username: '',
      password: '',
      confirmPassworld: '',
    };
  },
  methods: {
    openRegisterModal() {
      this.isRegisterModalOpen = true;
    },
    closeRegisterModal() {
      this.isRegisterModalOpen = false;
      this.resetForm();
    },
    resetForm() {
      this.username = '';
      this.password = '';
      this.confirmPassword = '';
    },
    async submitRegistration() {
      if (this.password !== this.confirmPassword) {
        alert('两次输入的密码不一致，请重新输入!');
        return;
      }

      try {
        const response = await axios.post('http://47.102.116.125:8080/register', {
          username: this.username,
          password: this.password,
        });

        // 根据后端实际返回的状态码和响应体进行处理
        switch (response.status) {
          case 201: // 用户注册成功
            alert('注册成功啦，请登录哦');
            break;
          case 400: // 请求参数错误
          case 409: // 用户名已存在
            alert('注册失败啦，呜呜呜，提示信息：' + response.data.error);
            break;
          default: // 其他服务器错误
            alert('注册过程中发生错误，请稍后再试试');
        }
      } catch (error) {
        console.error('注册失败', error);
        alert("注册过程中发生网络或其他未知错误，请稍后再试试");
      }
    },
  },
};
</script>

<style scoped>
.header {
  display: flex;
  align-items: center;
  background-color: white;
  padding: 20px 0;
}

.logo {
  width: 250px;
  height: 30px;
  margin-left: 10px;
}

.logo .nav-item {
  font-size: 30px;
  color: #333;
  cursor: pointer;
}

.nav {
  margin-top: 10px;
}

.nav .nav-item {
  font-size: 20px;
  color: #333;
  cursor: pointer;
  margin: 0 20px;
}

.nav .nav-item:hover {
  text-decoration: underline;
}

.nav .nav-item.active {
  color: #df0e0e
}

.action .nav-item {
  font-size: 16px;
  font-weight: 500;
  color: #333;
  cursor: pointer;
  margin: 0 15px;
  padding: 10px 15px;
  border-radius: 4px;
  background-color: transparent;
  transition: background-color 0.2s ease-in-out;
}

.action .nav-item:hover,
.action .nav-item:focus {
  color: white;
  background-color: #333;
  text-decoration: none;
  outline: none;
}

/* 注册按钮 */
.register-btn {
  display: inline-block;
}

.register-btn .icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 9px;
}

.register-btn .icon::before {
  content: '\f007'; /* 使用Font Awesome的"user-plus"图标 */
  font-family: 'Font Awesome 5 Free', sans-serif;
  font-weight: 900;
  font-size: 18px;
}

/* 登录按钮 */
.login-btn {
  display: inline-block;
}

.login-btn .icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 9px;
}

.login-btn .icon::before {
  content: '\f007'; /* 使用Font Awesome的"sign-in-alt"图标 */
  font-family: 'Font Awesome 5 Free', sans-serif;
  font-weight: 900;
  font-size: 18px;
}

/* 颜色变换交互效果 */
.action .nav-item:hover,
.action .nav-item:focus {
  color: #ffffff;
  background-color: #007BFF; /* 使用亮蓝色作为悬停/聚焦背景色 */
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 2rem;
  border-radius: 7px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  width: 300px;
}

.modal-content form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-content button {
  width: 100%;
}
</style>